* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  box-sizing: border-box;
}

body, html {
  height: 100%;
  margin: 0;
  background-color: black;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.calculator {
  width: 350px;
  padding: 20px;
  border-radius: 15px;
  background-color: #333;
}

.display {
  background-color: black;
  color: white;
  border-radius: 10px;
  text-align: right;
  margin-bottom: 20px;
  padding: 10px;
}

.result {
  font-size: 3rem;
}

.input-field {
  width: 100%;
  font-size: 2rem;
  border: none;
  outline: none;
  background-color: transparent;
  color: white;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.button {
  background-color: #505050;
  border: none;
  color: white;
  font-size: 1.5rem;
  border-radius: 10px;
  padding: 20px;
  cursor: pointer;
  text-align: center;
  aspect-ratio: 1 / 1; /* To keep buttons square */

}

.button:hover {
  background-color: #686868;
}

.btn-function {
  background-color: #f39c12;
  color: white;

}

.btn-function:hover {
  background-color: #e67e22;
}

